body {
 margin:0;
 padding:0;
 font-family: $fontfam1;
 font-size: 16px;
 line-height: 1.33;
 color: #333;
 background-color: white;
 border:none!important;
 transition:$transitionall-slow;
 overflow-x:hidden;
 /* width:100vh; */
 /* position:fixed; */
 .body-container-wrapper {
  padding-top:$navheight;
  @mixin atSm {
   padding-top:$navheight-sm;
  }
  @mixin atMd {
   padding-top:0;
  }
 }
}
#content {
 width:100%;
}
.body-container-wrapper {
 position: relative;
 background-color: white;
 max-width:100vw;
 padding-bottom: 1.5rem;
 /* z-index:-2; */
}
.body-container-wrapper, .footer-container-wrapper {
 transition:$transitionall-slow;
}

.header-container-wrapper {
 width:100%;
 overflow-x:hidden;
 background-color: white;
}
img {
 position:relative;
 max-width:100%;
 margin-left: auto;
 margin-right:auto;
 &:only-child {
  text-align:center;
 }
}
a {
 color:$charcoal;
}



body.hs-site-page:after {
 opacity:0.5;
 position:fixed;
 bottom:1rem;
 right:1rem;
 display:block;
 padding:0.25rem 0.375rem;
 content:'<';
 font-size:0.875rem;
 line-height: 1;
 background-color: grey;
 color:white;
 @mixin atSm {
  content:'sm';
  background-color: pink;
 }
 @mixin atSmmd {
  content:'smmd';
  background-color: violet;
 }
 @mixin atMd {
  content:'md';
  background-color: cadetblue;
 }
 @mixin atLg {
  content:'lg';
  background-color: orange;
 }
}
