   .hs_cos_wrapper_type_text {
    width:50%;
    bottom:0;
    position: absolute;
    display:inline-block;
    @mixin atSm {
     }
    @mixin atSmmd {
     }
    @mixin atMd {
     transform: translateY(-0.1ex);
     }
    @mixin atLg {
     transform: translateY(-0.33ex);
     }
